	  url: '/we-need-a-reset',
	  heading: ['BC Government needs a reset.'],
	  variable: ['Our schools, universities, colleges, health organizations and transportation services are facing difficult realities. To provide the services we expect, they need to become more agile, more efficient and more focused. Over the next three years over $70\xA0billion[1] will be proivided to service delivery agents in these sectors. How much will be squandered on political jockeying, and pointless paperwork?', 'We need our policymakers to wake up to the twenty-first century. We need to take advantage of technology to make our Government better. Both our dear Liberal party and it\'s well intentioned opposition are unable. We need an alternative that can think outside the box and make structural changes.'],
